import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/actions';
import { PostInfo, AuthorInfo } from '../models/model';
import { PostState, AuthorState } from './states';



export const initialState: PostState = { postInfo: [] };

export function reducer(state = initialState, action: fromActions.PostAction): PostState {
  switch(action.type) {
    case fromActions.POSTS: {
      return {
          ...state,
          postInfo: action.payload
        };
    }
    default: {
      return state;
    }
  }	
}

export const getPostState = createFeatureSelector<PostState>('postState');

export const getPosts = createSelector(
    getPostState, 
    (state: PostState) => state.postInfo 
);


