import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/actions';
import { PostInfo, AuthorInfo } from '../models/model';
import { PostState, AuthorState } from './states';

export const initialState: AuthorState = { authorInfo: [] };

export function reducer(state = initialState, action: fromActions.AuthorAction): AuthorState {
  switch(action.type) {
    case fromActions.AUTHOR: {
      return {
          ...state,
          authorInfo: action.payload,
        };
    }
    default: {
      return state;
    }
  }	
}

export const getAuthorState = createFeatureSelector<AuthorState>('authorState');

export const getAuthor = createSelector(
    getAuthorState, 
    (state: AuthorState) => state.authorInfo 
);


