import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';

import { BlogComponent } from './blog/blog.component';

import { FormsModule } from '@angular/forms';

import { FlexLayoutModule } from '@angular/flex-layout';
import { AppRoutingModule } from './app-routing.module';

import {
  MatButtonModule, MatCardModule, MatIconModule,
  MatListModule, MatMenuModule, MatTooltipModule,
  MatSlideToggleModule, MatInputModule, MatCheckboxModule,
  MatToolbarModule, MatSnackBarModule, MatSidenavModule,
  MatTabsModule, MatSelectModule, MatRadioModule,
  MatAutocompleteModule, MatFormFieldModule, MatChipsModule,
  MatDialogModule, MatGridListModule, MatTableModule, MatPaginatorModule,
  MatProgressBarModule, MatProgressSpinnerModule
} from '@angular/material';


const MATERIAL_MODULES: any[] = [
  MatButtonModule, MatCardModule, MatIconModule,
  MatListModule, MatMenuModule, MatTooltipModule,
  MatSlideToggleModule, MatInputModule, MatCheckboxModule,
  MatToolbarModule, MatSnackBarModule, MatSidenavModule,
  MatTabsModule, MatSelectModule, MatRadioModule,
  MatAutocompleteModule, MatFormFieldModule, MatChipsModule,
  MatDialogModule, MatGridListModule, MatTableModule, MatPaginatorModule,
  MatProgressBarModule, MatProgressSpinnerModule
];

import {
  CovalentCommonModule, CovalentLayoutModule, CovalentMediaModule, CovalentExpansionPanelModule,
  CovalentStepsModule, CovalentLoadingModule, CovalentDialogsModule, CovalentSearchModule, CovalentPagingModule,
  CovalentNotificationsModule, CovalentMenuModule, CovalentDataTableModule, CovalentMessageModule
} from '@covalent/core';

import { ServicesComponent } from './pages/services/services.component';

import { StoreModule } from '@ngrx/store';
import { reducers} from './ng/reducers/reducers';
import { Blog2Component } from './blog2/blog2.component';
import { SearchComponent } from './search/search.component';

const COVALENT_MODULES: any[] = [
  CovalentCommonModule, CovalentLayoutModule, CovalentMediaModule, CovalentExpansionPanelModule,
  CovalentStepsModule, CovalentDialogsModule, CovalentLoadingModule, CovalentSearchModule,
  CovalentPagingModule, CovalentNotificationsModule, CovalentMenuModule, CovalentDataTableModule,
  CovalentMessageModule,
];


@NgModule({
  declarations: [
    AppComponent,
    SearchComponent,
    BlogComponent,
    ServicesComponent,
    Blog2Component
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MATERIAL_MODULES,
    COVALENT_MODULES,
    FlexLayoutModule,
    FormsModule,
    AppRoutingModule,
    
    // LazyLoadModule,
    StoreModule.forRoot(reducers)
  ],
  exports: [
    MATERIAL_MODULES,
    COVALENT_MODULES,
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



