import { Component, OnInit, AfterContentInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { PostInfo, AuthorInfo } from '../ng/models/model';
import { PostState, AuthorState } from '../ng/reducers/states';
import { Store } from '@ngrx/store';
import { Router, ActivatedRoute } from '@angular/router';
import { TdLoadingService } from '@covalent/core';

import * as PostReducer from '../ng/reducers/post.reducer';
import * as AuthorReducer from '../ng/reducers/author.reducer';
import * as fromActions from '../ng/actions/actions';
import * as steem from "steem";

@Component({
  selector: 'app-blog2',
  templateUrl: './blog2.component.html',
  styleUrls: ['./blog2.component.css']
})
export class Blog2Component implements OnInit, AfterContentInit {

  selectTag: String;

  static DATE = new Date().toISOString().split('.')[0];
  static GET_SIZE = 10;

  postInfo: Observable<PostInfo[]>;
  authorInfo: Observable<AuthorInfo[]>;

  static spostStore: Store<PostState>;
  static sauthorStore: Store<AuthorState>;
  static srouter: Router;
  static sid: String;

  id: String;
  private sub: any;


  constructor(
    private route: ActivatedRoute,
    private _loadingService: TdLoadingService,
    private postStore: Store<PostState>,
    private authorStore: Store<AuthorState>,
    private router: Router
  ) {

    this.postInfo = postStore.select(PostReducer.getPosts);
    this.authorInfo = authorStore.select(AuthorReducer.getAuthor);

    Blog2Component.sauthorStore = this.authorStore;
    Blog2Component.spostStore = this.postStore;
    Blog2Component.srouter = this.router;

    this.selectTag = 'all_all';
  }


  ngAfterContentInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = params['id']; // (+) converts string 'id' to a number

      // In a real app: dispatch action to load the details here.
    });

    console.log(this.id);

    this.searchID(this.id);
  }

  ngOnInit() {

    // SearchComponent.srouter.navigateByUrl('/blog/asinayo');

  }

  clickTag(tag: String) {
    this.selectTag = tag;
    console.log(this.selectTag);
    // this.selectedPosts = this.tagList.get(tag);
  }

  searchID(searchWord: String) {

    Blog2Component.sid = searchWord;
    // this.loading.push("Loading")
    Blog2Component.getAuthorInfo(searchWord);

    let pi: PostInfo = {
      postList: new Array(),
      tagMap: new Map(),
      tagList: new Array()
    }

    Blog2Component.getNextPosts2(searchWord, null, pi);

  }

  static getAuthorInfo(searchWord: String) {
    steem.api.getAccounts([searchWord], function (err, response) {

      let ai: AuthorInfo = {};

      if (err != undefined) {
        console.log(err);
      } else {
        ai.author = response[0];

        if (ai.author.json_metadata.length <= 0 || ai.author.json_metadata != undefined) {
          ai.meta = {
            profile: {
              name: ai.author.name,
            }
          }
        } else {
          ai.meta = JSON.parse(ai.author.json_metadata);
        }


        console.log(ai.author);

        // Blog2Component.sauthorStore.dispatch(new fromActions.AuthorAction(ai));
      }


    });
  }

  static getNextPosts2(author: String, startPermlink: any, pi: PostInfo) {

    // console.log(startPermlink + " -- " +date );
    steem.api.getDiscussionsByAuthorBeforeDateAsync(author, startPermlink, Blog2Component.DATE, Blog2Component.GET_SIZE, function (err, result) {
      // console.log(result);
      var post = null;
      var lastPermlink: any;
      for (var indexI = 0; indexI < result.length; ++indexI) {
        post = result[indexI];
        lastPermlink = post.permlink;

        if (indexI + 1 < result.length) {
          // console.log(post.author + " --- " + post.root_title);
          pi.postList.push(post);
        }

      }

      if (result.length >= Blog2Component.GET_SIZE || false) {
        // console.log(lastPermlink);
        Blog2Component.getNextPosts2(author, lastPermlink, pi);
      } else {
        for (var indexI = 0; indexI < pi.postList.length; ++indexI) {
          // console.log(pi.postList[indexI]);
          console.log(pi.postList[indexI].author + " --- " + pi.postList[indexI].root_title);
        }
        Blog2Component.sortTag2(pi);
      }
    });

  }

  static sortTag2(pi: PostInfo) {

    var post: any = null;

    pi.tagMap['all_all'] = new Array();
    pi.tagList.push('all_all');


    for (var indexI = 0; indexI < pi.postList.length; ++indexI) {
      post = pi.postList[indexI];

      pi.tagMap['all_all'].push(post);

      var tags = JSON.parse(post.json_metadata).tags;

      for (var indexJ = 0; indexJ < tags.length; ++indexJ) {

        if (pi.tagMap[tags[indexJ]] === undefined) {
          pi.tagMap[tags[indexJ]] = new Array();
          pi.tagList.push(tags[indexJ]);
        }
        pi.tagMap[tags[indexJ]].push(post);

      }

    }

    console.log('END');
    
    // Blog2Component.sauthorStore.dispatch(new fromActions.PostAction(pi));
    // BlogComponent.srouter.navigateByUrl('/blog/'+BlogComponent.sid);



  }


}



