import { Component, OnInit, AfterContentInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material';
import { TdMediaService } from '@covalent/core';

import { HostBinding, ChangeDetectionStrategy } from '@angular/core';

import * as steem from "steem";
import { TdLoadingService } from '@covalent/core';
import { Observable } from 'rxjs/Observable';
import { PostInfo, AuthorInfo } from '../ng/models/model';
import { Store } from '@ngrx/store';
import { PostState, AuthorState } from '../ng/reducers/states';

import * as fromActions from '../ng/actions/actions';

import * as PostReducer from '../ng/reducers/post.reducer';
import * as AuthorReducer from '../ng/reducers/author.reducer';

export interface SibModel {
  postList: Array<any>;
  tagMap: Map<String, Array<any>>;
  tagList: Array<String>;
  load: number;
  date: String;
  size: number;
  author: any;
  meta: any;
}



@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.scss']
})
export class BlogComponent implements OnInit, AfterContentInit {

  selectTag: String;

  static DATE = new Date().toISOString().split('.')[0];
  static GET_SIZE = 10;

  postInfoList: Observable<PostInfo[]>;
  authorInfoList: Observable<AuthorInfo[]>;

  static spostStore: Store<PostState>;
  static sauthorStore: Store<AuthorState>;
  static srouter: Router;
  static sid: String;

  id: String;
  private sub: any;

  selectedPosts: Array<any> = new Array();

  constructor(
    private route: ActivatedRoute,
    private _loadingService: TdLoadingService,
    private postStore: Store<PostState>,
    private authorStore: Store<AuthorState>,
    private router: Router
  ) {

    this.postInfoList = postStore.select(PostReducer.getPosts);
    this.authorInfoList = authorStore.select(AuthorReducer.getAuthor);

    BlogComponent.sauthorStore = this.authorStore;
    BlogComponent.spostStore = this.postStore;
    BlogComponent.srouter = this.router;

    
    
    this.selectTag = 'all_all';
  }


  ngAfterContentInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = params['id']; // (+) converts string 'id' to a number

      // In a real app: dispatch action to load the details here.
    });

    console.log(this.id);

    this.searchID(this.id);
  }

  ngOnInit() {

    // SearchComponent.srouter.navigateByUrl('/blog/asinayo');

  }

  

  clickTag(tag: String) {
    this.selectTag = tag;
    console.log(this.selectTag);
    this.selectedPosts = this.postInfoList[0].tagMap.get(tag);
  }

  searchID(searchWord: String) {

    BlogComponent.sid = searchWord;
    // this.loading.push("Loading")
    BlogComponent.getAuthorInfo(searchWord);

    let pi: PostInfo = {
      postList: new Array(),
      tagMap: new Map(),
      tagList: new Array()
    }

    BlogComponent.getNextPosts2(searchWord, null, pi);

  }

  static getAuthorInfo(searchWord: String) {
    steem.api.getAccounts([searchWord], function (err, response) {

      let ai: AuthorInfo = {};

      if (err != undefined) {
        console.log(err);
      } else {
        ai.author = response[0];

        if (ai.author.json_metadata.length <= 0 || ai.author.json_metadata == undefined) {
          ai.meta = {
            profile: {
              name: ai.author.name,
            }
          }
        } else {
          ai.meta = JSON.parse(ai.author.json_metadata);
        }


        // console.log(ai.author);

        ai.author.last_post = BlogComponent.convertDate(ai.author.last_post);

        let arr: Array<AuthorInfo> = new Array();
        arr.push(ai);
        console.log(arr);
        BlogComponent.sauthorStore.dispatch(new fromActions.AuthorAction(arr));
      }


    });
  }

  static getNextPosts2(author: String, startPermlink: any, pi: PostInfo) {

    // console.log(startPermlink + " -- " +date );
    steem.api.getDiscussionsByAuthorBeforeDateAsync(author, startPermlink, BlogComponent.DATE, BlogComponent.GET_SIZE, function (err, result) {
      // console.log(result);
      var post = null;
      var lastPermlink: any;
      for (var indexI = 0; indexI < result.length; ++indexI) {
        post = result[indexI];
        lastPermlink = post.permlink;

        if (indexI + 1 < result.length) {
          // console.log(post.author + " --- " + post.root_title);
          pi.postList.push(post);
        }

      }

      if (result.length >= BlogComponent.GET_SIZE || false) {
        // console.log(lastPermlink);
        BlogComponent.getNextPosts2(author, lastPermlink, pi);
      } else {
        // for (var indexI = 0; indexI < pi.postList.length; ++indexI) {
          // console.log(pi.postList[indexI]);
          // console.log(sm.postList[indexI].author + " --- " + sm.postList[indexI].root_title);
        // }
        BlogComponent.sortTag2(pi);
      }
    });

  }

  static convertDate(date: any): String {
    let index = String(date).indexOf('T');
    let retDate = String(date).substring(0, index);
    return retDate;
  }

  static sortTag2(pi: PostInfo) {

    var post: any = null;

    pi.tagMap['all_all'] = new Array();
    pi.tagList.push('all_all');


    for (var indexI = 0; indexI < pi.postList.length; ++indexI) {
      post = pi.postList[indexI];

      pi.tagMap['all_all'].push(post);

      post.created = BlogComponent.convertDate(post.created);
      
      post.json_metadata = JSON.parse(post.json_metadata);

      var tags = post.json_metadata.tags;

      for (var indexJ = 0; indexJ < tags.length; ++indexJ) {

        if (pi.tagMap[tags[indexJ]] === undefined) {
          pi.tagMap[tags[indexJ]] = new Array();
          pi.tagList.push(tags[indexJ]);
        }
        pi.tagMap[tags[indexJ]].push(post);

      }

    }

    console.log('END');
    
    let arr: Array<PostInfo> = new Array();
    arr.push(pi);
    // console.log(arr);
    BlogComponent.sauthorStore.dispatch(new fromActions.PostAction(arr));
    // BlogComponent.srouter.navigateByUrl('/blog/'+BlogComponent.sid);



  }

}



